import { observer } from 'mobx-react-lite';
import React, { useContext } from 'react';
import { Context } from '..';
import ListGroup from 'react-bootstrap/ListGroup';
import { Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { REST_ROUTE } from "../utils/consts";

const TypeBar = observer(() => {
    const { menuitem } = useContext(Context);
    const navigate = useNavigate();

    const Menu = () => {
        menuitem.setSelectedType({});
        navigate(REST_ROUTE);
    };

    return (
        <div className="p-4 border rounded bg-light shadow-sm mt-3">
            <h4 className="text-center mb-3">Выберите Тип</h4>
            <Button variant="primary" className="w-100 mb-3" onClick={Menu}>
                Меню
            </Button>

            <ListGroup as="ol" numbered>
                {menuitem.types.map(type => (
                    <ListGroup.Item
                        as="li"
                        className="d-flex justify-content-between align-items-start mb-2"
                        style={{ cursor: 'pointer', transition: 'background-color 0.2s' }}
                        active={type.id === menuitem.selectedType.id}
                        onClick={() => menuitem.setSelectedType(type)}
                        key={type.id}
                        onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#e9ecef'}
                        onMouseLeave={(e) => e.currentTarget.style.backgroundColor = ''}
                    >
                        <div className="ms-2 me-auto">
                            <div className="fw-bold">{type.name}</div>
                        </div>
                    </ListGroup.Item>
                ))}
            </ListGroup>
        </div>
    );
});

export default TypeBar;